.gallery {
    width: 100%;
    max-width: 900px;
    margin: 0 auto;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.inner {
    display: flex;

}

.item-img {
    min-height: 200px;
    min-width: 400px;
    padding: 14px;
}

.item-img img {
    width: 100%;
    height: 90%;
    border-radius: 15px;
    pointer-events: none;
}

.carousel {
    cursor: grab;
    overflow: hidden;
}

@media screen and (max-width:870px) {
    .item-img {
        min-height: 461px;
        min-width: 334px !important;

    }
}