
.container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

h1 {
    margin-bottom: 5px;
}

form {
    display: flex;
    flex-direction: column;
}

.selectContainer, .containerObs, .containerInput{
    background-color: rgba(150, 45, 110, 0.384);
    padding: 8px;
    margin-bottom: 5px;
    display: flex;
    border-radius: 8px;
    box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.5);
}
.containerInput input {
    min-width: 100%;
    min-height: 37px;
}

.containerInput input::placeholder{
    color: #807c7c;
}


.selectContainer select{
   min-width: 100%;
}

.containerObs textarea{
    background: transparent;
    border: 0;
    outline: none;
    color: #807c7c;
}

selct{
    color: #807c7c;
}

input , select{
    min-height: 30px;
    max-width: 220px;
    outline: none;
    border: 0;
    background: transparent;
    
}

#observacao {
    margin-bottom: 5px;
}

.form-group {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 0.8em;
}

.form-group > p {
    margin-top: 2px;
    margin-bottom: 0;
}

.opt_ct_fidelidade {
    display: flex;
    flex-direction: row;
    max-height: 21px;
    margin-bottom: 3px;
}

.form-check{
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    flex-direction: row;
    margin-left: 5px;
}