
.nav-bg{
    background-color: rgb(192, 97, 141);
    border: 0;
    margin: -0.0px;
}

.gallery-center h1{
    
    text-align: center;
}

nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgba(243, 235, 235, 0.897);
    padding: 20px 80px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06);
    border-style: solid;
    border-radius: 0px 0px 60px 60px;
    border: 10px;
}

#navbar {
    display: flex;
    align-items: center;
    justify-content: center;

}



#navbar li {
    list-style: none;
    padding: 0 10px;
    position: relative;

}

#navbar li a {
    text-decoration: none;
    font-size: 1.3rem;
    font-weight: 600;
    color: #552610;
    transition: 0.3s ease-in-out;
}

#navbar li a:hover, 
#navbar li a.active{
    color: violet;
}

#navbar li a:hover::after, #navbar li a:hover::after{
    content: "";
    width: 30%;
    height: 2px;
    background-color: blueviolet;
    position: absolute;
    bottom: -4px;
    left: 20px;
}

#mobile {
    display: none;
}

#mobile i{
    color: #fff;
}

@media screen and (max-width:870px){
    #navbar {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        position: fixed;
        top: 78px;
        right: -300px;
        width: 300px;
        height: 65vh;
        background: #c04876bd;
        box-shadow: 0 40px 60px rgba(0, 0, 0, 0.1);
        padding: 40px 0 0 10px;
        transition: 0.3s ease-in-out;    
        border-radius: 30px 0px 0 30px;
        z-index: 1;
    }

    #navbar.active{
        right: 0px;
    }

    #navbar li {
        margin-bottom: 25px;
    }

    #mobile {
        display: block;

    }

    #mobile i{
        font-size: 24px;
        cursor: pointer;

    }
    
}